import uniIcon from '@/components/uni-icon/uni-icon.vue';
import uniIcons from "@/components/uni-icons/uni-icons.vue"
import { post } from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			codeText:'获取验证码',
			code_status:false,
			loginname: '',
			telephone: '',
			verificationCode: '',
			password: '',
			re_password: '',
			title: '注册',
			promoterid: 0
		};
	},
	components: { uniIcon,uniIcons },
	onLoad() {
		if (uni.getStorageSync('promoterid')) {
			this.promoterid = uni.getStorageSync('promoterid');
			console.log(this.promoterid);
		} else {
			console.log('没有推荐人');
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.switchTab({
				url: '/pages/my/my'
			});
		},
		login_action() {
			uni.reLaunch({
				url: '/pages/my/login/login'
			});
		},
		handleReg() {
			const { loginname, password, telephone, re_password, promoterid,verificationCode} = this;
			if (loginname.length == 0) {
				this.$api.msg('登录账号不能为空！');
				return;
			}
			if (telephone.length != 11) {
				this.$api.msg('手机号码不正确！');
				return;
			}
			if (verificationCode.length == 0) {
				this.$api.msg('手机验证码不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('登录密码不能为空！');
				return;
			}
			if (re_password.length == 0) {
				this.$api.msg('重复密码不能为空！');
				return;
			}
			if (password != re_password) {
				this.$api.msg('两次密码不一致！');
				return;
			}
		
			const sendData = {
				loginname,
				password,
				telephone,
				verificationCode,
				promoterid
			};
			this.loading = true;
			post(api.Regist, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						let userInfo = {
							token: res.data.data.token,
							exp: res.data.data.exp,
							userId: res.data.data.userId
						};
						uni.setStorage({
							key: 'userInfo',
							data: userInfo,
							success: function() {
								uni.switchTab({
									url: '/pages/my/my'
								});
							}
						});
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		},
		send() {
			const telephone = this.telephone;
			if(telephone.length!=11){
				this.$api.msg('请输入手机号！');
				return;
			}
			this.code_status = true;
			const sendData = {
				telephone:this.telephone,
				sendType:1
			};
			post(api.GetSmsCode, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.settime(60);
					} else {
						this.$api.msg(res.data.returnMessage);
						this.code_status = false;
					}
				})
				.catch(error => {
					this.$api.msg('请求失败fail');
				});
		},
		settime(smiao) {
			const miao = this.miao;
			if (miao != 0) {
				setTimeout(() => {
					smiao--;
					this.miao = smiao;
					this.codeText = this.miao+'秒后重新获取';
					this.settime(smiao);
				}, 1000);
			}else{
				this.code_status = false;
				this.codeText='获取验证码'
				this.miao = 60;
			}
		}
	}
};
